* {
    margin: 0px;
    box-sizing: border-box;
    border: 1px solid black;
}

.linha::after {
    clear: both;
    content: "";
    display: block;
}

/*Mobile First*/
[class^="col-"] {
    float: left;
    padding: 0px;
    width: 100%;
}

/*Tablet*/
@media only screen and (min-width:600px) {
    .col-dm-1 {
        width: 8.33%;
    }
    .col-dm-2 {
        width: 16.66%;
    }
    .col-dm-3 {
        width: 25%;
    }
    .col-dm-4 {
        width: 33.33%;
    }
    .col-dm-5 {
        width: 41.66%;
    }
    .col-dm-6 {
        width: 50%;
    }
    .col-dm-7 {
        width: 58.33%;
    }
    .col-dm-8 {
        width: 66.66%;
    }
    .col-dm-9 {
        width: 74.99%;
    }
    .col-dm-10 {
        width: 83.32%;
    }
    .col-dm-11 {
        width: 91.65%;
    }
    .col-dm-12 {
        width: 100%;
    }
}
    
/*PC*/
@media only screen and (min-width:1000px) {
    .col-dg-1 {
        width: 8.33%;
    }
    .col-dg-2 {
        width: 16.66%;
    }
